/* Container for the Shop page */
.shop-container {
    padding: 2rem;
    background-color: #fcdccc; /* Light background to highlight items */
    border-radius: 8px; /* Rounded corners for a softer look */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

/* Search and filter section */
.shop-search-filter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

/* Input and select styles */
.shop-search-filter input {
    width: 60%; /* Adjusted width for input */
    padding: 0.75rem; /* Increased padding for a better touch target */
    font-size: 1rem;
    border: 1px solid #ff5722; /* Border color to match theme */
    border-radius: 20px;
    transition: border-color 0.3s;
    margin-right: 10px;
}

.shop-search-filter input:focus {
    border-color: #ff1744; /* Darker red on focus */
    outline: none; /* Remove default outline */
}

/* Grid layout for item cards */
.shop-item-cards {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem; /* Increased gap for better spacing */
}

/* Individual item card */
.shop-item-card {
    display: flex;
    flex-direction: column; 
    flex: 1 1 calc(25% - 1.5rem); /* Two cards per row on larger screens */
    background-color: #fff; /* Keep cards white for contrast */
    border: 1px solid #ffccbc; /* Soft border for items */
    border-radius: 8px; /* Rounded corners */
    overflow: hidden; /* Prevent overflow */
    padding: 1rem;
    text-align: left;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15); /* Increased shadow for depth */
    transition: transform 0.3s, box-shadow 0.3s; /* Animation effects */
}

.shop-item-card:hover {
    transform: translateY(-5px); /* Slight lift on hover */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Enhanced shadow on hover */
}

/* Image styles */
.shop-item-image {
    width: 120px; 
    height: 120px; 
    object-fit: cover; 
    border-radius: 8px; 
    margin: 0 auto; 
    transition: transform 0.3s; /* Animation effect */
}

.shop-item-card:hover .shop-item-image {
    transform: scale(1.05); /* Slight zoom on hover */
}

/* Details container */
.shop-item-details {
    flex: 1; 
    padding: 0 1rem; /* Space between image and details */
}

/* Title, Description, and Price */
.shop-item-details h3 {
    margin: 0;
    font-size: 1.5rem; /* Increased size for emphasis */
    color: #d32f2f; /* Dark red for titles */
    text-align: center;
}

/* Description text truncation */
.shop-item-details p {
    margin: 0.5rem 0;
    color: #666; /* Soft gray for description */
    display: -webkit-box;
    -webkit-line-clamp: 2; /* Show only 2 lines */
    -webkit-box-orient: vertical; /* Vertical layout for the box */
    overflow: hidden; /* Hide overflow text */
    text-overflow: ellipsis; /* Add ellipsis (...) at the end */
}


.shop-item-price {
    font-weight: bold;
    color: #c62828; /* Darker red for price */
}

/* Buttons */
.shop-item-actions {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.shop-item-actions .btn {
    background-color: #ff5722; /* Bright orange */
    color: #fff;
    border: none;
    padding: 0.75rem 1.25rem; /* Increased padding */
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 0.9rem;
    margin: 0.5rem 0;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    transition: background-color 0.3s, transform 0.3s; /* Animation effects */
}

.shop-item-actions .btn:hover {
    background-color: #e65100; /* Darker Orange */
    transform: scale(1.05); /* Slight zoom on hover */
}

/* Responsive design for mobile screens */
@media (max-width: 600px) {
    .shop-item-card {
        flex-direction: column; /* Stack image and details vertically */
        flex: 1 1 100%; /* Single card per row on smaller screens */
        padding: 0%;
    }

    .shop-item-image {
        width: 100%; /* Increase size for mobile */
        height: calc(100vw - 20vw); /* Increase size for mobile */
        border-radius: 0px;
    }

    .shop-item-details {
        padding: 0.7rem; /* Increase padding for better touch target */
    }

    .shop-search-filter {
        flex-direction: column; /* Stack input and selects on mobile */
        align-items: flex-start; /* Align items to the start */
        gap: 1rem; /* Space between input and select */
    }

    .shop-search-filter input,
    .shop-search-filter select {
        width: 100%; /* Full width for inputs on mobile */
        font-size: 0.9rem; /* Reduce font size for mobile */
    }
    .shop-item-actions .btn {
       
        font-size: 0.7rem;
        padding: 0.5rem 0.75rem; /* Increased padding */
        margin: 0.2rem 0.2rem;
        
    }
}

/* Responsive design for larger screens */
@media (min-width: 601px) {
    .shop-item-image {
        width: 150px; /* Adjust width for larger screens */
        height: 150px; /* Adjust height for larger screens */
    }
}

@media (min-width: 900px) {
    .shop-item-image {
        width: 200px; /* Further adjust width for even larger screens */
        height: 200px; /* Further adjust height for even larger screens */
    }
}

/* Price container */
.price-container {
    display: flex;
    align-items: center;
    gap: 10px;
}

/* Original price with strike-through */
.original-price {
    text-decoration: line-through;
    color: #888;
    font-size: 14px;
}

/* Final price */
.shop-item-price {
    font-size: 18px;
    font-weight: bold;
    color: #000;
}

