/* Modal overlay */
.modal-overlay {
    position: fixed; /* Fixed to cover the entire viewport */
    padding: 2rem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000; /* Ensure the modal is on top */
}

/* Modal content */
.modal-content {
    position: sticky;
    top: 0;
    background: #fff;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 60%; /* Use 60% width */
    max-height: 55vh; /* Limit height to 55% of viewport height */
    overflow-y: auto; /* Allow scrolling if content exceeds max height */
    scrollbar-width: none; /* Hide scrollbar for Firefox */
}

/* Hide scrollbar for WebKit browsers (Chrome, Safari) */
.modal-content::-webkit-scrollbar {
    display: none; /* Hide scrollbar */
}

/* Close button */
.modal-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
    color: #333;
}

.modal-close:hover {
    color: #f57c00;
}

/* Product card in modal */
.product-card {
    margin-top: 1.5rem;
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    text-align: left;
}

.product-card img {
    width: 100px;
    height: auto;
    object-fit: contain;
    border-radius: 8px;
}

.product-card h4 {
    margin: 0 0 0.5rem;
    font-size: 1.25rem;
}

.product-card p {
    margin: 0.5rem 0;
}

/* Quantity controls */
.quantity-controls {
    display: flex;
    align-items: center;
    gap: 10px;
}

.quantity-buttons {
    background-color: #135fb0;
    color: white;
    border: 1px solid #ccc;
    padding: 5px;
    cursor: pointer;
    font-size: 1.25rem;
    border-radius: 4px;
}

.quantity-buttons:hover {
    background-color: #074c96;
}

.quantity-button:disabled {
    cursor: not-allowed;
    background-color: #f7f7f7;
}

.product-card .quantity {
    font-size: 1.25rem;
}

/* Input fields */
.inputText {
    width: 100%;
    padding: 0.5rem;
    margin-top: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

/* Proceed button */
.proceed-button {
    background-color: #f57c00;
    color: #fff;
    border: none;
    padding: 0.75rem 1.5rem;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.25rem;
    margin-top: 1.5rem;
}

.proceed-button:hover {
    background-color: #d84315;
}

/* Total amount display */
.total-amount {
    margin-top: 1rem;
    font-size: 1.25rem;
    font-weight: bold;
}

/* Responsive adjustments */
@media (max-width: 600px) {
    .modal-content {
        padding: 1rem; /* Reduce padding for smaller screens */
        max-width: 90%; /* Use 90% width for smaller screens */
        max-height: 60vh; /* Limit height to 60% of viewport height */
    }

    .modal-close {
        font-size: 1.25rem; /* Adjust close button size */
    }

    .product-card img {
        width: 80px; /* Smaller image for small screens */
    }

    .proceed-button {
        font-size: 1rem; /* Smaller button text */
    }
}

/* Loading overlay */
.loading-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999; /* Adjust z-index as needed */
    border-radius: 8px; /* Match form border radius */
}

.loading-overlay img {
    width: 50px;
    height: 50px;
}

/* Preorder message styling */
.preorder-message {
    background-color: #ff9800; /* Orange background to grab attention */
    color: white; /* White text for contrast */
    padding: 10px 15px; /* Padding around the text */
    border-radius: 5px; /* Rounded corners */
    font-weight: bold; /* Make the text bold */
    font-size: 1rem; /* Increase the font size */
    margin-top: 10px; /* Space between the message and product info */
    display: flex;
    align-items: center;
    gap: 10px; /* Space between icon and text */
    animation: fadeIn 1s ease-out; /* Fade-in animation */
}

/* Icon styling */
.preorder-icon {
    font-size: 1.3rem; /* Larger icon for visibility */
    color: white; /* White color for the icon */
}

/* Fade-in animation */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* For mobile responsiveness */
@media (max-width: 600px) {
    .preorder-message {
        font-size: 0.9rem; /* Smaller font size on mobile */
    }

    .preorder-icon {
        font-size: 1.2rem; /* Slightly smaller icon */
    }
}
