/* Apply the color theme to the product detail container */
.product-detail-container {
    padding: 2.5rem;
    background-color: #fcdccc; /* Light background to highlight items */
    border-radius: 8px; /* Rounded corners for a softer look */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    display: flex;
    flex-direction: row;
    gap: 20px;
    font-family:'Times New Roman', Times, serif;
  }
  
  /* For mobile view, stack the content vertically */
  .product-detail-container.mobile {
    flex-direction: column;
    text-align: center;
  }
  
  .image-slider {
    flex: 1;
    max-width: 500px;
  }
  
  .image-slider img {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .product-info {
    flex: 1;
    max-width: 500px;
    padding: 10px;
    padding-top: 0%;
  }
  
  .product-info h2 {
    font-size: 2rem;
    margin-bottom: 15px;
    color: #d32f2f; /* Dark red color */
  }
  
  .product-info p {
    font-size: 1.2rem;
    line-height: 1.5;
    color: #666;
  }
  
  .price-quantity h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #c62828; /* Darker red for price */
  }
  
  .buttons {
    display: flex;
    gap: 15px;
    margin-top: 20px;
  }
  
  .buttons button {
    padding: 12px 25px;
    background-color: #ff5722; /* Bright orange */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
  }
  
  .buttons button:hover {
    background-color: #e65100; /* Darker Orange */
  }
  
  .other-details {
    background-color: #fff;
    padding: 15px;
    margin-top: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .other-details h4 {
    font-size: 1.5rem;
    color: #d32f2f;
  }
  
  .other-details ul {
    list-style-type: none;
    padding: 0;
  }
  
  .other-details li {
    font-size: 1.1rem;
    margin: 5px 0;
  }
  
  .other-details li strong {
    color: #333;
  }
  
  /* Responsive design for mobile */
  @media (max-width: 600px) {
    .product-detail-container {
      flex-direction: column;
      text-align: center;
    }
  
    .image-slider {
      max-width: 100%;
    }
  
    .product-info {
      padding: 15px;
    }
  
    .buttons button {
      width: 100%;
    }
  }
  

  /* Return Policy Section */
.return-policy {
  margin-top: 20px;
  padding: 15px;
  background-color: #f1f8e9; /* Light green background for the policy section */
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.return-policy p {
  font-size: 1.1rem;
  color: #333;
}

.return-policy .learn-more-link {
  display: inline-block;
  margin-top: 10px;
  font-size: 1.1rem;
  color: #1976d2; /* Blue color for the link */
  text-decoration: none;
  cursor: pointer;
}

.return-policy .learn-more-link:hover {
  text-decoration: underline;
}
