/* Container for the Cart page */
.cart-container {
    padding: 2rem;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px; /* Limit max-width for larger screens */
}

/* Title of the cart page */
.cart-container h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

/* Container for cart items */
.cart-items {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

/* Individual cart item card */
.cart-item-card {
    display: flex;
    align-items: center;
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fff;
    position: relative;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add shadow */
    transition: box-shadow 0.3s ease; /* Smooth transition on hover */
}

/* Cart item image */
.cart-item-card img {
    width: 100px;
    height: auto;
    object-fit: cover;
    border-radius: 8px;
}

/* Container for cart item details */
.cart-item-details {
    margin-left: 1rem;
}

/* Item title, description, and price */
.cart-item-details h3 {
    margin: 0;
    font-size: 1.25rem;
}

.cart-item-details p {
    margin: 0.5rem 0;
}

.cart-item-price {
    font-weight: bold;
    color: #333;
}

/* Container for item actions */
.cart-item-actions {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
}

/* Quantity buttons */
.cart-quantity-button {
    background-color: #135fb0;
    color: #fff;
    border: none;
    padding: 0.5rem 0.5rem;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.cart-quantity-button svg {
    font-size: 1.25rem;
}

/* Remove button */
.remove-button {
    background-color: #f57c00;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.remove-button svg {
    font-size: 1.25rem;
}

/* Buy Now button */
.buy-now-button {
    background-color: #f57c00;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.buy-now-button svg {
    font-size: 1.25rem;
}

/* Checkout button */
.checkout-button {
    background-color: #f57c00;
    color: #fff;
    border: none;
    padding: 0.5rem 0.5rem;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.25rem;
    margin-top: 2rem;
}

/* Responsive design for small screens */
@media (max-width: 600px) {
    .cart-container {
        width: 100%;
        padding: 2rem; /* Adjust padding for smaller screens */
    }
    
    /* Individual cart item card */
    .cart-item-card {
        flex-direction: column; /* Change to column layout for better responsiveness */
        align-items: center; /* Center align items */
        text-align: center; /* Center text for better alignment */
    }
    
    /* Cart item image */
    .cart-item-card img {
        max-width: 100%; /* Ensure image fits the card */
    }
    
    /* Container for cart item details */
    .cart-item-details {
        margin: 1rem 0;
    }
    
    /* Item actions */
    .cart-item-actions {
        flex-wrap: wrap; /* Allow actions to wrap on smaller screens */
        gap: 0.5rem;
    }
    
    /* Quantity buttons */
    .quantity-button,
    .remove-button,
    .buy-now-button {
        font-size: 0.875rem; /* Adjust button font size */
    }
    
    /* Checkout button */
    .checkout-button {
        font-size: 1rem; /* Adjust checkout button font size */
    }
}

/* Responsive design for medium screens */
@media (max-width: 800px) {
    .cart-container {
        width: 90%; /* Slightly reduce width for medium screens */
    }
}

.cartLoginInvalid{
    padding: 2rem;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px; /* Limit max-width for larger screens */
}
