/* General Body Style */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: #121212; /* Dark background */
    color: #fff;
}

/* Container for the user profile */
.user-profile-container {
    height: 100vh;
    background: linear-gradient(to right, #b71c1c, #d32f2f); /* Dark Red Gradient */
    padding-top: 0px;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Profile Card */
.user-profile {
    padding: 2rem;
    max-width: 600px;
    width: 100%;
    background-color: #2c2c2c; /* Dark background for the card */
    border-radius: 16px;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3); /* Stronger shadow for dark theme */
    transition: transform 0.3s ease-in-out;
}

.user-profile:hover {
    transform: scale(1.02);
}

/* Header Styling */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    flex-wrap: wrap; /* Allows header items to wrap on smaller screens */
}

.UP-header-h1 {
    font-size: 2rem;
    color: #fff;
    text-transform: uppercase;
    flex: 1;
    margin: 0;
}

.orders-btn {
    background-color: #fff;
    color: #d32f2f;
    font-size: 1rem;
    padding: 0.6rem 1.2rem;
    border: 2px solid #d32f2f;
    border-radius: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    transition: all 0.3s ease;
    margin-top: 1rem;
    flex-shrink: 0;
}

.orders-btn:hover {
    background-color: #d32f2f;
    color: #fff;
}

/* Form Group */
.form-group {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    flex-wrap: wrap; /* Allows form groups to wrap on smaller screens */
}

.form-group label {
    flex: 1;
    font-size: 1rem;
    color: #fff;
    margin-right: 1rem;
}

.form-group input,
.form-group select,
.form-group textarea {
    flex: 2;
    padding: 0.8rem;
    border: 1px solid #444;
    border-radius: 8px;
    background-color: #333;
    color: #fff;
    font-size: 1rem;
    transition: background-color 0.3s ease;
    width: 100%; /* Makes inputs full-width */
}

.form-group input:disabled,
.form-group select:disabled,
.form-group textarea:disabled {
    background-color: #555;
    color: #bbb;
}

/* Button Styles */
.form-group button {
    padding: 0.75rem 1.5rem;
    background-color: #d32f2f;
    color: #fff;
    border: none;
    border-radius: 50px;
    font-size: 1rem;
    cursor: pointer;
    margin-right: 0.5rem;
    transition: background-color 0.3s ease;
    width: 100%; /* Full-width button on small screens */
}

.form-group button:hover {
    background-color: #b71c1c;
}

.form-group button:disabled {
    background-color: #777;
    cursor: not-allowed;
}

/* Buttons Container */
.buttons {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; /* Stack buttons on small screens */
}

.edit-btn,
.save-btn,
.cancel-btn {
    padding: 0.75rem 1.5rem;
    background-color: #d32f2f;
    color: #fff;
    border: none;
    border-radius: 50px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 48%; /* Slightly reduced width for two buttons in a row */
    margin-top: 1rem;
}

.edit-btn:hover,
.save-btn:hover,
.cancel-btn:hover {
    background-color: #b71c1c;
}

.cancel-btn {
    background-color: #616161;
}

.cancel-btn:hover {
    background-color: #424242;
}

/* Responsive Design Adjustments */

/* Tablet and mobile responsiveness */
@media (max-width: 768px) {
    /* Adjust profile card padding and max-width for tablets */
    .user-profile {
        padding: 1.5rem;
        width: 90%;
        max-width: 100%;
    }

    /* Adjust header font size and button */
    h1 {
        font-size: 1.8rem;
    }

    .orders-btn {
        font-size: 0.9rem;
        padding: 0.5rem 1rem;
    }

    /* Form Group label and input adjustments */
    .form-group label {
        font-size: 0.95rem;
        margin-right: 0.5rem;
    }

    .form-group input,
    .form-group select,
    .form-group textarea {
        font-size: 0.95rem;
        padding: 0.7rem;
    }

    /* Buttons (edit/save/cancel) stacking and font size reduction */
    .buttons {
        flex-direction: column;
        align-items: stretch;
    }

    .edit-btn,
    .save-btn,
    .cancel-btn {
        width: 100%;
        font-size: 0.9rem;
    }

    /* Orders button wrapping on smaller screens */
    .orders-btn {
        width: 100%;
    }
}

/* Mobile responsiveness */
@media (max-width: 480px) {
    /* Further reduction in padding and font size */
    .user-profile {
        padding: 1rem;
        width: 100%;
    }

    h1 {
        font-size: 1.6rem;
    }

    .orders-btn {
        font-size: 0.85rem;
        padding: 0.5rem 1rem;
    }

    /* Form label and input adjustments */
    .form-group label {
        font-size: 0.9rem;
        margin-right: 0.5rem;
    }

    .form-group input,
    .form-group select,
    .form-group textarea {
        font-size: 0.9rem;
        padding: 0.6rem;
    }

    /* Buttons should occupy full width on small screens */
    .edit-btn,
    .save-btn,
    .cancel-btn {
        width: 100%;
        font-size: 0.85rem;
    }

    .buttons {
        flex-direction: column;
        align-items: stretch;
    }

    /* Orders button should be full-width */
    .orders-btn {
        width: 100%;
    }
}
